import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "2.0.2";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>Bug fixes and improvements</Title>
                    <Line/>
                    Several variables related to date/time have been added to the filename template.
                    <br/>
                    Fixed an issue where the app would crash under certain situations.
                    <br/>
                    Fixed automatic app shutdown and automatic system shutdown not working under certain situations.
                    <br/>
                    Some bugs were fixed.
                    <br/>
                    Some features were improved.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}