import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "2.0.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>대규모 업데이트</Title>
                    <Line/>
                    [일반]
                    <br/>
                    <br/>
                    네트워크 속도가 향상되었습니다.
                    <br/>
                    상세한 로그 기록을 볼 수 있습니다.
                    <br/>
                    <Important>메모리(RAM)가 과도하게 사용되는 문제를 해결하였습니다.</Important>
                    <br/>
                    소프트웨어 최적화 및 안정화를 진행하였습니다.
                    <br/>
                    <br/>
                    <br/>
                    [검색]
                    <br/>
                    <br/>
                    검색 결과를 새로고침할 수 있습니다.
                    <br/>
                    <Important>여러 검색 결과를 탭으로 유지할 수 있습니다.</Important>
                    <br/>
                    특정 조건에서 검색 기능이 비디오 아이디와 클립 아이디를 올바르게 구별하지 못하는 문제를 해결하였습니다.
                    <br/>
                    <br/>
                    <br/>
                    [설정]
                    <br/>
                    <br/>
                    특정 조건에서 데이터가 올바르게 저장되지 않는 문제를 해결하였습니다.
                    <br/>
                    <br/>
                    <br/>
                    [다운로드]
                    <br/>
                    <br/>
                    <Important>동시에 여러 다운로드를 진행할 수 있습니다.</Important>
                    <br/>
                    <Important>다운로드 우선순위를 설정할 수 있습니다.</Important>
                    <br/>
                    <Important>확률적으로 다운로드가 완료되지 않는 문제(주로 99%에서 멈추는 문제)를 해결하였습니다.</Important>
                    <br/>
                    <Important>실시간 다운로드가 생방송 광고를 차단하여 다운로드합니다.</Important>
                    <br/>
                    <Important>실시간 다운로드의 안정성을 확보하였습니다.</Important>
                    <br/>
                    파일 형식에 따라 이전에 사용한 폴더, 확장자, 추가 옵션을 유지합니다.
                    <br/>
                    다운로드 완료 이후 수행할 작업을 예약할 수 있습니다.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}