import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "2.2.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>New features and bug fixes</Title>
                    <Line/>
                    <Important>Added a new feature that allows you to quickly download with the previous option without opening the download menu.(Useful when doing a lot of downloads.)</Important>
                    <br/>
                    <Important>You can retry a failed or canceled download.</Important>
                    <br/>
                    <Important>You can now view and manage your download history.</Important>
                    <br/>
                    You can start a new download based on your download history.
                    <br/>
                    Improved Update Tracking mode.
                    <br/>
                    Improved video cropping.
                    <br/>
                    <Important>You can optimize your video.(Maintains video quality but significantly reduces file size.)</Important>
                    <br/>
                    Fixed an issue where the app would crash under certain situations.
                    <br/>
                    Some bugs were fixed.
                    <br/>
                    Some features were improved.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}