import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "2.2.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>새로운 기능 및 버그 수정</Title>
                    <Line/>
                    <Important>다운로드 메뉴 창을 열지 않고 이전 옵션으로 빠르게 다운로드할 수 있는 기능이 추가되었습니다.(대량의 다운로드를 진행할 때 유용합니다.)</Important>
                    <br/>
                    <Important>실패하거나 취소한 다운로드를 다시 시도할 수 있습니다.</Important>
                    <br/>
                    <Important>다운로드 기록을 보고 관리할 수 있습니다.</Important>
                    <br/>
                    다운로드 기록을 바탕으로 새 다운로드를 시작할 수 있습니다.
                    <br/>
                    업데이트 추적 모드가 개선되었습니다.
                    <br/>
                    비디오 자르기 기능이 개선되었습니다.
                    <br/>
                    <Important>비디오를 최적화할 수 있습니다.(비디오의 화질은 유지하지만 파일 용량이 크게 줄어듭니다.)</Important>
                    <br/>
                    특정 상황에서 앱이 강제 종료되는 문제를 해결하였습니다.
                    <br/>
                    일부 버그가 수정되었습니다.
                    <br/>
                    일부 기능이 개선되었습니다.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}