import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "2.0.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>Massive Update</Title>
                    <Line/>
                    [General]
                    <br/>
                    <br/>
                    Network speed has been improved.
                    <br/>
                    You can view detailed logs.
                    <br/>
                    <Important>Fixed an issue with excessive memory(RAM) usage.</Important>
                    <br/>
                    Software optimized and stabilized.
                    <br/>
                    <br/>
                    <br/>
                    [Search]
                    <br/>
                    <br/>
                    You can refresh the search results.
                    <br/>
                    <Important>You can keep multiple search results in tabs.</Important>
                    <br/>
                    Fixed an issue where the search function would not correctly distinguish between video IDs and clip IDs under certain conditions.
                    <br/>
                    <br/>
                    <br/>
                    [Settings]
                    <br/>
                    <br/>
                    Fixed an issue where data was not saved correctly under certain conditions.
                    <br/>
                    <br/>
                    <br/>
                    [Download]
                    <br/>
                    <br/>
                    <Important>Supports parallel downloads.</Important>
                    <br/>
                    <Important>You can set the download priority.</Important>
                    <br/>
                    <Important>Fixed an issue where downloads could not be completed in a probability(mostly stuck at 99%).</Important>
                    <br/>
                    <Important>Live download blocks ads.</Important>
                    <br/>
                    <Important>Secured stability of live downloads.</Important>
                    <br/>
                    Keeps previously used folders, extensions, and additional options depending on the file type.
                    <br/>
                    You can schedule an action to take place after all downloads are complete.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}