import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "2.0.2";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>버그 수정 및 개선</Title>
                    <Line/>
                    파일명 생성 규칙에 날짜/시간과 관련된 여러 변수가 추가되었습니다.
                    <br/>
                    특정 상황에서 앱이 강제 종료되는 문제를 해결하였습니다.
                    <br/>
                    앱 자동 종료와 시스템 자동 종료가 특정 상황에서 작동하지 않는 문제점을 해결하였습니다.
                    <br/>
                    일부 버그가 수정되었습니다.
                    <br/>
                    일부 기능이 개선되었습니다.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}